    </router-link>
    <router-link tag="div" class="tab-item" to="/TVplay">
      <span class="tab-link">电视剧</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/film">
      <span class="tab-link">电影</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/comic">
      <span class="tab-link">动漫</span>
    </router-link>
      <router-link tag="div" class="tab-item" to="/variety">
      <span class="tab-link">综艺</span>
    </router-link>
    <router-link tag="div" class="tab-item" to="/sport">
      <span class="tab-link">体育</span>
    </router-link>
  </div>
</template>
<script>
    export default {}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  .tab
    display: flex
    height: 44px
    line-height: 44px
    font-size: $font-size-medium
    .tab-item
      flex: 1
      text-align: center
      .tab-link
        padding-bottom: 5px
        color: $color-text-l
      &.router-link-active
        .tab-link
          color: $color-theme
          border-bottom: 2px solid $color-theme
</style>





