<script>
	import axios from "axios"
    import {params,callback,url} from "api/getFilm"
    import Scroll from "base/scroll/scroll"
   export default {
   	 data(){
        return {
          TVs:[],
          TVs1:[],
          TVs2:[],
          TVs3:[],
          TVs4:[],
          TVs5:[],
          TVs6:[],
          TVs7:[],
          TVs8:[],
          TVs9:[],
          TVs10:[],
          TVs11:[]
          
          
        }
      },
       components:{
        Scroll
      },
    	created(){
        this._getRecommend()
     },
      methods:{    
        _getRecommend(){
          this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
                this.TVs=res.body.rec[0].videos
                this.TVs1=res.body.rec[1].videos
                this.TVs2=res.body.rec[2].videos
                this.TVs3=res.body.rec[3].videos
                this.TVs4=res.body.rec[4].videos
                this.TVs5=res.body.rec[5].videos
                this.TVs6=res.body.rec[6].videos
                this.TVs7=res.body.rec[7].videos
                this.TVs8=res.body.rec[8].videos
                this.TVs9=res.body.rec[9].videos
                this.TVs10=res.body.rec[10].videos
                this.TVs11=res.body.rec[11].videos
                
          })
        },
       
      }
    }
   
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
