<template>
  <div class="video">
	 	<scroll class="recommend-content" :data="forum" >
	      <div >
			    <h1 class="lovetile">
				    <i></i>
				    <span>最新热播</span>
			    </h1>
			    <ul class="clearfix">
			      <li  class="love" v-for="item in recommends">
			      	<a :href="item.linkurl">
				        <img :src="item.picurl">
				        <div class="love-title">
				         	<h3 class="titleone" v-html="item.vidtitle"></h3>
				         	<h4 class="titletwo" v-html="item.subtitle"></h4>
				        </div>
				      </a>
			      </li>
			    </ul>
			     <h1 class="lovetile">
				    <i></i>
				    <span>体育</span>
			    </h1>
			    <div class="lovecon">
			      <div class="love" v-for="item in sports
			      	">
				      <a :href="item.playurl">
				        <img :src="item.focusPic">
				        <h3 class="titleone" v-html="item.title"></h3>
				        <h4 class="titletwo" v-html="item.beginTime"></h4>
				        <h4 class="titletwo" v-html="item.endTime"></h4>
				        </a>
			      </div>
			    </div>	
			    
			    <h1 class="lovetile">
				    <i></i>
				    <span>发布会</span>
			    </h1>
			    <div class="lovecon">
			      <div class="love" v-for="item in release">
				      <a :href="item.playurl">
				        <img :src="item.focusPic">
				        <h3 class="titleone" v-html="item.title"></h3>
				        <h4 class="titletwo" v-html="item.beginTime"></h4>
				        <h4 class="titletwo" v-html="item.endTime"></h4>
				        </a>
			      </div>
			    </div>	
			    <h1 class="lovetile">
				    <i></i>
				    <span>论坛</span>
			    </h1>
			    <div class="lovecon">
			      <div class="love" v-for="item in forum">
				      <a :href="item.playurl">
				        <img :src="item.focusPic">
				        <h3 class="titleone" v-html="item.title"></h3>
				        <h4 class="titletwo" v-html="item.beginTime"></h4>
				        <h4 class="titletwo" v-html="item.endTime"></h4>
				        </a>
			      </div>
			    </div>	
	   	 </div>
	    <div class="loading-container" v-show="!recommends.length">
        <loading></loading>
      </div>
	  </scroll>
	  
  </div>
</template>
<script>
		import axios from "axios"
    import {params,callback,url} from "api/getVideo"
    import {release,releaseBack,releaseUrl} from "api/getRelease"
    import Scroll from "base/scroll/scroll"
    import Loading from "base/loading/loading"
   export default {
   	 data(){
        return {
          recommends:[],
          release:[],
          forum:[],
          sports:[]
        }
      },
       components:{
        Scroll,
        Loading
        
      },
    	created(){
    		this._getRelease(),
        this._getRecommend()
      },
      methods:{    
        _getRecommend(){
          this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
              this.recommends = res.body.rec,
             this._getRelease()
          })
        }, 
        _getRelease(){
          this.$http.jsonp(releaseUrl,{release:release,"jsonp":releaseBack}).then((res)=>{
                this.release = res.body.brandInfo
                this.forum = res.body.carousels
                this.sports = res.body.sportsInfo
          })
        }
       
      }
    }
   
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
   @import "~common/stylus/variable"
  .video
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .recommend-content
      height: 100%
      overflow: hidden
      .lovetile{
      	padding-left: 50px;
      	margin: 10px 0;
      	font-size: 24px;
      	color: deepskyblue;
      	
      }
      .titleone{
      	font-size:14px ;
      	color: #333;
      	margin:10px 0 ;
      	padding-left:10px ;
      }
      .titletwo{
      	font-size:12px ;
      	padding-left:10px;
      }
      
      .love{
      	display: inline-block;
      	overflow: hidden;
      	margin:2px 0;
      	width: 48%;
      	padding-left: 20px;
      	box-sizing: border-box;
      }
      .love-title{
      	margin-top: 10px;
      	height: 50px;
      }
      .love a{
      	display: block;
      	width: 100%;
      }
      img{
      	display: block;
      	width: 100%;
      	height:120px;
      }
     
      
</style>





