<template>
  <div class="TV">
	 	<scroll class="TV-content" :data="Release">
	      <div>
	      	<h1 class="lovetile">
	      	<i></i>
	      	<span>发布会</span>
	      	</h1>
	        <div class="lovecon">
	        	<div class="love" v-for="item in Release">
		          <a :href="item.playurl">
		            <img :src="item.focusPic">
		            <h3 class="titleone" v-html="item.title"></h3>
		            <h4 class="titletwo" v-html="item.beginTime"></h4>
		            <h4 class="titletwo" v-html="item.endTime"></h4>
		          </a>
	        	</div>
	        </div>
	        
	      </div>
	  </scroll>
  </div>
</template>
<script>
	import axios from "axios"
    import {params,callback,url} from "api/getVIP"
    import Scroll from "base/scroll/scroll"
   export default {
   	 data(){
        return {
          Release:[]
        }
      },
       components:{
        Scroll
      },
    	created(){
        this._getRecommend()
      },
      methods:{    
        _getRecommend(){
          this.$http.jsonp(url,{params:params,"jsonp":callback}).then((res)=>{
                this.Release = res.body.brandInfo
          })
        },
       
      }
    }
   
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
   @import "~common/stylus/variable"

  .TV
    position: fixed
    width: 100%
    top: 88px
    bottom: 0
    .TV-content
      height: 100%
      overflow: hidden
      .lovetile{
      	padding-left: 50px;
      	margin: 10px 0;
      	font-size: 30px;
      	color: red;
      	
      }
      
      .love{
      	display: inline-block;
      	overflow: hidden;
      	margin:5px 0;
      	width: 48%;
      	padding-left: 20px;
      	box-sizing: border-box;
      }
      .titleone{
      	margin-top: 10px;
      	font-size:16px ;
      	color: #333;
      	/*height: 20px;*/
      	padding-left:10px ;
      }
      .titletwo{
      	font-size:14px ;
      	padding-left:10px ;
      	height: 20px;
      	margin-top: 5px;
      }
      img{
      	width: 100%;
      	height: 100%;
      }
     
</style>



